.Modal {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: flex;

    background-color: rgba(0,0,0, 0.5);

    &-ContentWrap {
        position: relative;
        display: flex;
        flex-direction: column;
        gap: 20px;
        min-width: 350px;
        min-height: 150px;
        margin: auto;
        padding: 30px;

        border-radius: 12px;
    }

    &-Header {
        display: flex;
        justify-content: center;
        align-items: center;
        padding-top: 10px;

        font-weight: 700;
        font-size: 18px;
    }

    &-BtnClose {
        position: absolute;
        top: 15px;
        right: 15px;
        font-size: 18px;

        background-color: transparent;
        border: none;
        border-radius: 4px;
        outline: none;

        transition: background-color .1s;
        cursor: pointer;

        &:hover {
            background-color: rgba(0,0,0, 0.05);
        }
    }

    &-Footer {
        display: flex;
        justify-content: center;
        gap: 15px;
        margin-top: auto;
    }
}
