.AdvertisementPage {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    margin-top: 25px;

    &-Wrap {
        display: flex;
    }

    &-ImagesBlock {
        flex-shrink: 0;
        width: 40%;
        margin-right: 50px;
    } 

    &-Content {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        gap: 25px;
        width: 100%;
    }

    &-Block {

    }

    &-BlockTitle {
        margin: 0;
        margin-bottom: 15px;
    }

    &-ImageWrap {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 20px;
        width: 100%;
    }

    &-Image {
        max-width: 100%;
        object-fit: cover;
    }

    &-List {
        display: flex;
        flex-wrap: wrap;
        gap: 8px;
        margin: 0;
        padding: 0;

        list-style: none;
    }

    &-ListItem {

    }

    &-Text {
        margin: 0;
    }

    &-Header {
        display: flex;
        justify-content: space-between;
        gap: 25px;
    }

    &-Actions {
        display: flex;
        gap: 15px
    }

    &-Address {
        display: flex;
        flex-direction: column;
        gap: 10px;
    }
}