.Button {
    --radius: 5px;

    display: flex;
    padding: 10px 15px;

    font-family: inherit;
    font-size: 16px;
    line-height: 1;
    color: #ffffff;

    border: none;
    background-color: rgb(38, 150, 241);

    transition: background-color .2s;
    cursor: pointer;

    &:hover {
        background-color: rgba(66,170,255, .85);
    }

    &_view_normal-flat {
        color: #000000;

        border: 1px solid rgb(38, 150, 241);
        background-color: #fff;

        &:hover {
            background-color: #42aaff26;
        }
    }

    &_link {
        text-decoration: none;
    }

    &_view_second {
        background-color: rgb(236, 236, 235);
        border: 1px solid #000;
        color: #000;

        &:hover {
            background-color: rgb(230, 230, 219);
        }
    }

    &_view_third {
        background-color: rgb(179, 243, 193);
        color: #000;
        border-radius: 7px;

        &:hover {
            background-color: rgb(170, 230, 183);
        }
    }

    &_view_normal {
        background-color: transparent;
        border: 1px solid #000;
        color: #000;

        &:hover {
            background-color: rgba(0,0,0, .05);
        }
    }

    &_view_outlined-danger {
        color: rgba(255, 4, 0, .8);
        border: 1px solid rgba(255, 4, 0, .8);
        background-color: transparent;

        &:hover {
            background-color: rgba(255, 4, 0, .05);
        }
    }

    &_view_flat {
        color: rgb(38, 150, 241);

        border-radius: 4px;
        background-color: transparent;

        &:hover {
            background-color: rgba(0,0,0, .05);
        }
    }

    // pin

    &_pin_round-round {
        border-radius: var(--radius);
    }

    &_pin_brick-brick {
        border-radius: 0;
    }

    &_pin_clear-clear {
        border-radius: 0;
        border-left: 0;
        border-right: 0;
    }

    &_pin_circle-circle {
        border-radius: 100px;
    }

    &_pin_round-brick {
        border-radius: var(--radius) 0 0 var(--radius);
    }

    &_pin_brick-round {
        border-radius: 0 var(--radius) var(--radius) 0;
    }

    &_pin_round-clear {
        border-radius: var(--radius) 0 0 var(--radius);
        border-right: 0;
    }

    &_pin_clear-round {
        border-radius: 0 var(--radius) var(--radius) 0;
        border-left: 0;
    }

    &_pin_brick-clear {
        border-radius: 0;
        border-right: 0;
    }

    &_pin_clear-brick {
        border-radius: 0;
        border-left: 0;
    }

    &_pin_circle-brick {
        border-radius: 100px 0 0 100px;
    }

    &_pin_brick-circle {
        border-radius: 0 100px 100px 0;
    }

    &_pin_circle-clear {
        border-radius: 100px 0 0 100px;
        border-right: 0;
    }

    &_pin_clear-circle {
        border-radius: 0 100px 100px 0;
        border-left: 0;
    }
}