.Advertisements {
    --gap-items: 15px;

    display: flex;
    flex-direction: column;

   
    width: 100%;
    height: 100%;
    margin-top: 25px;

    background-color: #ffffff;

    &-Content {
        display: flex;
        flex-wrap: wrap;
        width: 100%;
        gap: var(--gap-items);
        margin-top: 25px;
    }

    &-Actions {
        display: flex;
        justify-content: center;
        width: 100%;
        margin-top: 50px;
    }

    &-Advertisement {
        width: calc(25% - ( 3 * var(--gap-items) / 4));
    }

    &-Panel {
        display: flex;
        align-items: flex-end;
        padding-bottom: 25px;
        border-bottom: 1px solid rgba(0,0,0, 0.3);
    }

    &-Search {
        width: 100%;
    }
}