.Messages {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 15px;
    width: 100%;
    height: 100%;

    overflow-y: auto;

    &::-webkit-scrollbar-track {
        -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
        background-color: #f5f5f5;
    }
    
    &::-webkit-scrollbar {
        width: 8px;
        background-color: #f5f5f5;
    }
    
    &::-webkit-scrollbar-thumb {
        background-color: #aaa8a8;
    }

    &-Reserve {
        display: inline-flex;
        visibility: hidden;
    }

    &-Date {
        align-self: center;

        font-weight: 700;
        font-size: 12px;
    }
}