.Advertisement {
    color: inherit;
    text-decoration: none;
    transition: transform .2s;

    &:hover {
        transform: scale(1.05);
    }

    &-Image {
        display: flex;
        width: 100%;
        height: 300px;
        object-fit: cover;
        background-color: #c3c3c3;
    }

    &-Card {
        height: 100%;
    }
}
