.Menu {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-shrink: 0;
    width: 100%;
    min-height: 60px;
    padding: 20px;

    box-shadow: 0 0 5px rgba(0,0,0, 0.15);
    background-color: #ffffff;

    &-Group {
        display: flex;
        align-items: center;
        gap: 15px;
    }

    &-Profile {
        display: flex;
        align-items: center;
        gap: 10px;
    }

    &-BtnAuth {
        margin-left: 40px;
    }
}