.Chat {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: calc(100vh - 76px - 25px);
    margin-top: 25px;


    &-Content {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 80%;
        height: 100%;
        margin: 0 auto;
    }

    &-Header {
        width: 100%;
        
        border-top-left-radius: 12px;
        border-top-right-radius: 12px;
    }

    &-Actions {
        display: flex;
        width: 100%;
    }

    &-Input {
        width: 100%;
        padding: 10px;

        outline: none;
    }

    &-BtnSend {
        flex-shrink: 0;
    }
}