.Filter {
    position: relative;

    &-Card {
        position: absolute;
        top: calc(100% + 10px);
        right: 0;
        width: 325px;
        padding: 10px;

        background-color: rgba(255, 255, 255, 1);

        &_hidden {
            display: none;
        }
    }

    &-Form {
        align-items: center;
    }
}