.Message {
    position: relative;
    max-width: 85%;
    padding: 12px 15px;

    border: 1px solid rgb(179, 243, 193);
    border-radius: 12px;

    &_my {
        align-self: flex-end;
    }

    &-Date {
        padding-left: 5px;

        font-weight: 700;
        font-size: 12px;

        visibility: hidden;

        &::after {
            content: attr(text);
            position: absolute;
            right: 8px;
            bottom: 6px;
            visibility: visible;
        }
    }
}