.Form {
    display: flex;
    flex-direction: column;
    gap: 25px;

    padding: 25px;

    &-Close {
        position: absolute;
        top: 10px;
        right: 10px;

        font-size: 18px;

        background-color: transparent;
        border: none;
        border-radius: 4px;
        outline: none;

        transition: background-color .1s;
        cursor: pointer;

        &:hover {
            background-color: rgba(0,0,0, 0.05);
        }
    }

    &-TextInput {
        display: flex;
        flex-direction: column;
        width: 100%;
    }

    &-Label {
       margin-bottom: 10px;
    }

    &-Required {
        margin-left: 2px;
        color: #ff0000;
    }

    &-Input {
        width: 100%;
        padding: 0 8px 8px;

        border: none;
        border-bottom: 1px solid #000;
        outline: none;

        box-shadow: inset 20px 20px 0px 20px #ffffff;
        transition: border-bottom-color .2s;

        &:focus {
            border-bottom-color: rgb(175, 250, 156);
        }
    }

    &-FileInput {
        display: flex;
        flex-direction: column;
    }

    &-BtnGroup {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        gap: 7px;
        width: 100%;
    }
}