.Chats {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    margin-top: 25px;

    &-Chats {
        display: flex;
        flex-direction: column;
        gap: 20px;
        height: 100%;
    }

    &-Link {
        color: inherit;
        text-decoration: none;
    }

    &-Chat {
        transition: box-shadow .2s;
        &:hover {
            box-shadow: 0 0 5px rgba(66,170,255, .85);
        }
    }

    &-Receiver {
        display: flex;
        justify-content: space-between;
    }

    &-Message {
        margin-top: 10px;
    }

}